<template>
  <div class="r-custom-scroll" :style="`height: ${maxHeight}px`" v-if="show">
    <pdf
      ref="pdfData"
      style="margin: auto; margin-bottom: 20px"
      @error="resize = false"
      :resize="resize"
      scale.sync="100%"
      :src="pdfdata"
      v-for="i in numPages"
      :key="i"
      :id="i"
      :page="i"
    ></pdf>
  </div>
</template>
<script>
// import pdf from "pdfvuer";
import apiClient from "@/client/index.js";

export default {
  props: {
    path: {
      type: String,
      default: null
    }
  },
  components: {
    pdf
  },
  computed: {
    maxHeight() {
      return window.innerHeight - 280;
    }
  },
  watch: {
    path() {
      this.pdfdata = undefined;
      this.show = false;
      this.getPdf();
    }
  },
  mounted() {
    this.pdfdata = undefined;
    this.getPdf();
  },
  data: () => ({
    resize: false,
    numPages: 0,
    pdfdata: undefined,
    show: true
  }),
  methods: {
    getPdf() {
      if (this.path === null) {
        return;
      }
      apiClient.getPdf(this.path).then(res => {
        const url = window.URL.createObjectURL(new Blob([res.data]));
        this.pdfdata = pdf.createLoadingTask(url);
        this.pdfdata.then(pdf => {
          this.resize = true;
          this.numPages = pdf.numPages;
          this.show = true;
          this.setPage(pdf.numPages);
        });
      });
    },
    setPage(source) {
      this.$emit("setPageNumber", source);
    }
  }
};
</script>
